.card-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: var(--color-gray);
    /* Dark grey background */
    border-radius: 12px;
    padding: 12px 16px;
    margin-bottom: 12px;
    color: var(--color-white);
    /* Assuming Roboto or similar */
}

.info-section {
    display: flex;
    flex-direction: column;
    gap: 4px;
}

.title {
    margin: 0;
    font-size: 14px;
    font-weight: 500;
    color: var(--color-white);
}

.location {
    margin: 0;
    font-size: 12px;
    color: #fbbc04;
    /* Yellow/Orange accent */
    font-weight: 500;
}

.date-time {
    margin: 0;
    font-size: 10px;
    color: #888888;
}

.distance {
    margin: 0;
    font-size: 10px;
    color: #aaaaaa;
}

.actions-section {
    display: flex;
    gap: 8px;
}

.icon-circle {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    background-color: #444444;
    /* Slightly lighter grey for buttons */
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: background-color 0.2s;
}

.icon-circle:hover {
    background-color: #555555;
}

.icon-circle:active {
    transform: scale(0.9);
}

.icon-circle mat-icon {
    font-size: 18px;
    width: 18px;
    height: 18px;
    color: var(--color-light-gray);
    transition: color 0.2s ease;
}

.icon-circle mat-icon.saved {
    color: #fbbc04;
}